.calendar-section {
	$this: &;
	padding: 198px 0 104px;
	background-color: #F7F5F2;
	@include lg {
		padding: 198px 0 104px; }
	@include laptopSm {
		padding: 100px 0 60px; }
	@include xs {
		padding: 70px 0 50px; }
	&__title {
		margin-bottom: 30px;
		@include xs {
			margin-bottom: 20px; } }
	&__subtitle {
		text-align: center;
		font-size: 30px;
		margin-bottom: 84px;
		@include r(413) {
			font-size: 20px; }
		@include xs {
			margin-bottom: 25px; } }

	&__buttons {
		display: flex;
		flex-wrap: wrap;
		margin: -10px -10px 30px;
		@include xs {
			display: none; }
		.btn-sm {
			margin: 10px; } }
	&__calendar {
		overflow: auto;
		background-color: #fff;
		border-radius: 4px;
		box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.22);
		max-width: 840px;
		margin-left: auto;
		margin-right: auto; }

	&_news {
		padding: 250px 0 240px;
		background-color: #F1EFEA;
		@include laptopSm {
			padding: 140px 0 120px; }
		@include xs {
			padding: 118px 0 60px; }
		#{$this}__title {
			margin-bottom: 100px;
			@include xs {
				margin-bottom: 109px; } }
		#{$this}__calendar {
			background-color: transparent;
			box-shadow: none;
			max-width: 1400px;
			margin-left: auto;
			margin-right: auto; } }

	&_news-page {
		padding: 187px 0 200px;
		@include laptopSm {
			padding: 140px 0 120px; }
		@include xs {
			padding: 70px 0 60px; }
		#{$this}__calendar {
			margin-left: auto;
			margin-right: auto; } } }
.tiva-facebook-all-in-one {
	.tiva-facebook-feed {
		.facebook-albums {
			margin: 0 !important; }

		a {
			color: #FF4000;
			text-decoration: none; }
		.tiva-facebook-tab-bar {
			margin-bottom: 60px;
			@include xs {
				margin-bottom: 30px; }
			.btn {
				border: 1px solid #fff;
				color: #000;
				background-color: #fff;
				padding: 14px 25px;
				font-size: 20px;
				.fa {
					color: #FF1814;
					margin-right: 20px; }
				@include xs {
					padding: 8px 18px;
					font-size: 16px; }

				&.active, &:hover {
					color: #fff;
					.fa {
						color: #fff; } } } }

		.content {
			@include xs {
				font-size: 16px; } }
		.read-more {
			.btn {
				letter-spacing: 0;
				min-width: 310px; } } } }

.tiva-facebook-event-calendar {
	.tiva-calendar {
		.calendar-table {
			tr {
				&.day {
					td {
						color: #ec201c !important; } }
				th {
					font-size: 14px !important;
					height: 60px !important;
					background-color: #ec201c !important;
					@include r(1425) {
						font-size: 14px !important; }
					@include r(1200) {
						font-size: 12px !important; }
					@include xs {
						font-size: 10px !important;
						padding: 5px !important; } }
				td {
					@include xs {
						font-size: 13px;
						padding: 5px !important; } } }
			.calendar-day-today {
				background-color: #F0EFED !important; }

			.calendar-day-weekend {
				color: #ec201c !important; }

			.calendar-day-normal {
				height: 80px !important;
				@include r(1200) {
					height: 70px !important; }
				@include xs {
					height: 60px !important; } } } }
	.calendar-day-event {
		.tiva-event-tooltip {
			.event-name {
				color: #ec201c !important; } } }

	.tiva-event-detail-full {
		background-color: #fff !important;
		opacity: 1 !important;
		padding-top: 80px !important;
		&::after {
			content: '';
			display: table;
			clear: both; }
		.event-time, .event-date, .event-location {
			color: #ec201c !important; }
		.event-image {
			margin-right: 20px;
			@include sm {
				max-width: 100%;
				margin-right: 0; }
			img {
				max-width: 100%; } }
		.event-item {
			display: flex;
			@include sm {
				flex-direction: column; }
			&__left {
				width: 50%;
				flex-shrink: 0;
				@include sm {
					width: 100%; } } }
		.btn {
			font-size: 14px !important; } }

	.tiva-facebook-event-calendar-wrap {
		background-color: transparent !important;
		box-shadow: none !important; }

	.event-calendar-bar {
		background-color: #eeeeee;
		margin-bottom: 0px !important;


		.btn.back-calendar {
			right: auto !important;
			float: none !important;
			top: 10px;
			left: 20px;
			font-size: 0;
			border-color: transparent !important;
			background-color: transparent !important;
			background-position: center center;
			background-size: contain;
			background-image: url(../img/arrow-left.svg);
			background-repeat: no-repeat; } } }


.tiva-facebook-all-in-one {
	.btn-primary, .btn.active, .btn:hover {
		background-color: #ec201c !important;
		border-color: #ec201c !important; } }


.mfp-wrap {
	background-color: #f7f5f2; }

.mfp-bg {
	background: #f7f5f2 !important;
	opacity: 1 !important; }

.tiva-facebook-feed-popup {
	border-radius: 8px;
	width: 74% !important;
	padding: 30px 33px;
	padding-right: 5px;
	background-color: #fff;
	box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.19);
	@include xs {
		padding: 20px;
		width: 90% !important; }

	.go-facebook a,
	.share a {
		color: #FF3F00; }

	.popup-container {
		width: 100% !important;
		float: none !important;
		display: flex;
		@include xs {
			flex-direction: column; } }

	button.mfp-close {
		position: absolute !important;
		left: 0 !important;
		top: -105px !important;
		font-size: 0 !important;
		width: 45px !important;
		height: 40px !important;
		background-image: url(../img/arrow-left.svg);
		background-size: contain;
		background-position: center center;
		background-repeat: no-repeat;
		@include r(1300) {
			top: -60px !important; } }




	.popup-left {
		background-color: #fff !important;
		display: flex !important;
		align-items: center !important;
		justify-content: center !important;
		padding: 0 !important;
		width: 37% !important;
		@include xs {
			height: auto !important;
			min-height: auto !important;
			width: 100% !important; }

		img, video {
			position: static !important;
			transform: none !important;
			max-width: 100% !important; } }

	.popup-right {
		width: 63% !important;
		padding-left: 17px;
		margin-top: -8px;

		@include xs {
			margin-top: 0 !important;
			padding-left: 0 !important;
			width: 100% !important; }

		.tiva-facebook-feed {
			.facebook-post-item {
				.bottom-bar {
					position: static !important;
					padding: 0 !important; } } } } }

.mfp-arrow-left, .mfp-arrow-right {
	width: 38px !important;
	height: 70px !important;
	@include xs {
		width: 20px !important; }
	&::before {
		opacity: 0.1;
		margin-top: 0 !important;
		margin-left: 0 !important;
		top: 0 !important;
		width: 100% !important;
		height: 100% !important;
		border: none !important;
		background-image: url(../img/left.svg);
		background-size: contain;
		background-position: center center;
		background-repeat: no-repeat; }
	&::after {
		display: none !important; } }

.mfp-arrow-left {
	left: 84px !important;
	@include r(900) {
		left: 50px !important; }
	@include xs {
		left: 0 !important; } }

.mfp-arrow-right {
	transform: scale(-1, 1);
	right: 84px !important;
	@include r(900) {
		transform: scale(-1, 1) scale(0.75) !important; }
	@include xs {
		right: 20px !important; } }

.mfp-content {
	margin: 150px auto !important;
	@include xs {
		margin: 90px auto !important; } }



