.about-banner {
	$this: &;
	padding: 100px 0 184px;
	@include laptopSm {
		padding: 80px 0 144px; }
	@include xs {
		padding: 60px 0 110px; }
	&__title {
		@include semi-bold;
		font-size: 52px;
		line-height: 1.15;
		margin: 0 auto;
		max-width: 1220px;
		text-align: center;
		@include laptopSm {
			font-size: 48px; }
		@include md {
			font-size: 40px; }
		@include xs {
			font-size: 26px;
			line-height: 1.38;
			margin: 0 auto; }
		span {
			color: $theme; }
		+ #{$this}__button {
			margin-top: 69px;
			@include xs {
				margin-top: 40px; } } }
	&__button {
		text-align: center; } }
