.footer {
	background-color: #EC201C;
	background-size: cover;
	background-position: center;
	position: relative;

	.container {
		position: relative;
		z-index: 1; }

	&__bg {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		z-index: 0;
		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover; } }

	&__content {
		padding: 172px 0 171px;
		text-align: center;
		max-width: 1150px;
		margin: 0 auto;
		@include laptopSm {
			padding: 100px 0 174px;
			max-width: 850px; } }
	&__title {
		@include medium;
		color: #ffffff;
		font-size: 60px;
		line-height: 1.5;
		margin-bottom: 89px;
		text-align: center;
		@include laptopSm {
			font-size: 50px;
			line-height: 1.4;
			margin-bottom: 60px; }
		@include sm {
			font-size: 28px;
			line-height: 1.36; } }
	&__bottom {
		padding: 55px 0 20px;
		border-top: 1px solid rgba(255, 255, 255, 0.3);
		display: flex;
		justify-content: space-between;
		@include medium;
		font-size: 18px;
		color: #ffffff;
		@include laptopSm {
			padding: 25px 0 20px; }
		@include sm {
			flex-direction: column-reverse;
			align-items: center;
			font-size: 14px; }
		@include xs {
			padding: 10px 0; } }
	&__links {
		@include sm {
			margin-bottom: 10px; }
		@include xs {
			margin-bottom: 5px; }
		a {
			color: inherit;
			text-decoration: none;
			@include sm {
				text-decoration: underline; } } }
	&__rights {
		@include sm {
			opacity: 0.6; } } }
