.btn-sm {
	display: inline-block;
	@include semi-bold;
	padding: 10px 60px;
	color: #ffffff;
	text-decoration: none;
	border-radius: 4px;
	font-size: 20px;
	border: none;
	background-color: $theme;
	line-height: 2;
	transition: color 0.3s;
	position: relative;
	z-index: 1;
	overflow: hidden;
	@include hover {
		&:after {
			left: -49%; } }
	@include md {
		font-size: 18px;
		padding: 9px 50px; }
	&:after {
		content: '';
		z-index: -1;
		background: linear-gradient(105deg, transparent 0%, transparent 40%, #ff8204 40%, #ff8204 100%);
		@include coverdiv;
		transition: left 0.5s ease-in-out;
		transform: scaleX(2);
		left: 100%; }
	&_transparent {
		color: #000;
		background: transparent;
		border-bottom: 3px solid $theme;
		border-radius: 0;
		padding: 8px 26px 9px;
		&:after {
			content: none; }
		@include hover {
			color: $theme; } }
	&_light {
		background-color: #ffffff;
		color: #000; }
	&_theme-color {
		background-color: #ffffff;
		color: $theme; } }
