.container {
	$this: &;
	padding: 0 40px;
	margin: 0 auto;
	width: vw(1760);
	padding: 0 #{vw(40)};
	padding-left: vw(145);
	@include hd {
		width: 1760px;
		padding: 0 40px; }
	@include md {
		width: 100%;
		padding: 0 40px; }
	@include sm {
		padding: 0 16px; }

	.header &,
	.menu &,
	.hero__links &,
	.about-banner &,
	.footer &,
	.contacts-section &,
	.testimonials &,
	.calendar-section &,
	.team & {
		padding-left: vw(40);
		@include md {
			padding-left: 40px; }
		@include sm {
			padding-left: 16px; } }

	.hero__links & {
		display: flex;
		justify-content: space-between; } }
