.btn {
	display: inline-block;
	@include semi-bold;
	font-size: vw(24);
	line-height: 1.67;
	padding: #{vw(12)} #{vw(70)} #{vw(14)};
	border: 3px solid #ffffff;
	color: #ffffff;
	text-decoration: none;
	border-radius: 4px;
	transition: background-color 0.3s, color 0.3s, border-color 0.3s;
	text-align: center;
	@include hover {
		background-color: #ffffff;
		color: #000; }
	@include hd {
		font-size: 24px;
		padding: 12px 70px 14px; }
	@include r(1500) {
		font-size: 18px; }
	@include md {
		font-size: 24px;
		padding: 12px 70px 14px; }
	@include sm {
		color: #000;
		background-color: #ffffff; }
	@include xs {
		min-width: 300px;
		padding: 12px 10px 14px; }
	@include r(413) {
		min-width: 260px; }
	&_light {
		color: #000;
		background-color: #ffffff;
		@include hover {
			border-color: #000;
			background-color: #000;
			color: #ffffff; } }
	&_theme {
		border-color: $theme;
		color: $theme;
		@include hover {
			background-color: $theme;
			color: #ffffff; } } }

