.header {
	$this: &;
	z-index: 9;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding: 29px 0;
	transition: background-color 0.3s, transform 0.4s 0.6s ease-in-out;
	@include laptopSm {
		padding: 15px 0; }
	&.is-hidden {
		transform: translateY(-100%);
		transition: background-color 0.3s, transform 0.4s 0s ease-in-out; }
	&.is-scrolling, &_reg {
		background: #FFFFFF;
		box-shadow: 0 0 30px 0 rgba(0,0,0,0.30);
		.burger {
			span {
				background-color: #000; } }
		#{$this}__link {
			.icon {
				fill: #000000; } }
		.logo__image {
			&_active {
				opacity: 1; }
			&_static {
				opacity: 0; } }
		#{$this}__lang {
			color: #000; } }
	&.is-menu-open {
		box-shadow: none;
		background-color: transparent;
		@include md {
			background-color: #0C0C0C; }
		.burger {
			span {
				background-color: #ffffff; } }
		#{$this}__links {
			display: none; }
		.logo__image {
			&_active {
				opacity: 0; }
			&_static {
				opacity: 1; } }
		#{$this}__lang {
			color: #fff; } }
	&__inner {
		display: flex;
		justify-content: space-between; }
	&__logo {
		width: 160px;
		@include laptopSm {
			width: 120px; } }
	&__content {
		display: flex;
		align-items: center; }
	&__links {
		margin-right: 45px;
		transition: opacity 0.3s;
		@include xs {
			display: none; } }
	&__link {
		font-size: 34px;
		@include laptopSm {
			font-size: 28px; }
		.icon {
			transition: fill 0.3s;
			display: block;
			fill: #ffffff; }

		@include hover {
			.icon {
				animation: icon-up 0.8s linear; } } }

	&__langs {
		position: relative;
		margin-right: 45px;
		transition: margin 0.25s;
		@include xs {
			margin-right: 33px; }

		&.is-active {
			#{$this}__langs-list-inner {
				transform: translateX(0); }
			@include xs {
				margin-right: 10px; } } }
	&__lang {
		line-height: 1.2;
		font-family: inherit;
		appearance: none;
		background-color: transparent;
		text-decoration: none;
		color: #fff;
		font-size: 32px;
		@include semi-bold;
		display: block;
		text-transform: capitalize;
		transition: opacity 0.25s;
		@include laptopSm {
			font-size: 22px; } }
	&__langs-list {
		position: absolute;
		top: 0;
		right: 100%;
		overflow: hidden;

		#{$this}__lang {
			opacity: 0.5;
			margin-right: 26px;
			@include xs {
				margin-right: 5px; }

			@include hover {
				opacity: 1; } } }
	&__langs-list-inner {
		position: relative;
		display: flex;
		transition: transform 0.25s;
		transform: translateX(120%); } }

@keyframes icon-up {
	0% {
		transform: translateY(0); }
	50% {
		transform: translateY(-10px); }
	100% {
		transfom: translateY(0); } }
