.menu {
	$this: &;
	padding: 210px 0 80px;
	position: fixed;
	height: 100vh;
	height: calc(var(--vh) * 100);
	overflow-x: hidden;
	overflow-y: auto;
	top: 0;
	left: 0;
	z-index: 8;
	width: 100%;
	background-color: #0C0C0C;
	color: #ffffff;
	transform: translateY(-100%);
	transition: transform 0.4s ease-in-out;
	transition-delay: 500ms;
	@include lg {
		padding: 160px 0 60px; }
	@include md {
		padding: 100px 0 0; }
	&.is-active {
		transition-delay: 0s;
		transform: translateY(0);
		#{$this}__main {
			opacity: 1;
			transition-delay: 0.5s; }
		#{$this}__sidebar-links {
			opacity: 1;
			transition-delay: 0.8s; }
		#{$this}__socials {
			opacity: 1;
			transition-delay: 0.9s; }
		#{$this}__sidebar {
			&:before {
				transform: translateX(0);
				transition-delay: 0.5s; } } }
	&__inner {
		display: flex;
		@include md {
			flex-direction: column;
			justify-content: space-between;
			height: calc(100vh - 100px);
			height: calc(var(--vh) * 100 - 100px); } }
	&__main {
		transition: opacity 0.5s ease-in-out;
		opacity: 0;
		transition-delay: 0s;
		width: 64.3%;
		height: 100%;
		padding-right: 20px;
		@include md {
			height: auto;
			width: auto;
			padding: 0 0 40px; } }
	&__sidebar {
		padding: 0 20px 100px 81px;
		width: 35.7%;
		position: relative;
		min-height: calc(100vh - 290px);
		z-index: 1;
		@include lg {
			min-height: calc(100vh - 220px); }
		@include laptopSm {
			padding: 0 20px 100px 30px; }
		@include md {
			width: auto;
			min-height: auto;
			background-color: #1A1A1A;
			padding: 21px 40px 63px;
			margin: 0 -40px; }
		@include sm {
			padding: 21px 16px 63px;
			margin: 0 -16px; }
		@include xs {
			padding: 21px 16px 0; }
		&:before {
			z-index: -1;
			content: '';
			background-color: #1A1A1A;
			@include coverdiv;
			margin: -210px 0 -80px;
			padding: 210px 0 80px;
			width: 50vw;
			box-sizing: content-box;
			transform: translateX(100%);
			transition: transform 0.5s;
			transition-delay: 0.2s;
			@include lg {
				margin: -160px 0 -60px;
				padding: 160px 0 60px; }
			@include md {
				content: none; } } }
	&__sidebar-links {
		opacity: 0;
		transition: opacity 0.3s ease-in-out;
		transition-delay: 0s;
		@include md {
			margin-bottom: 50px; }
		@include xs {
			display: flex;
			margin: 0 -15px 30px; } }
	&__socials {
		position: absolute;
		bottom: 0;
		opacity: 0;
		transition: opacity 0.3s ease-in-out;
		transition-delay: 0s;
		@include md {
			position: static; }
		@include xs {
			background-color: #F7F5F2;
			padding: 15px 16px;
			margin: 0 -16px;
			.socials {
				justify-content: space-around;
				.icon {
					fill: #000; } } } }
	&__item {
		font-size: 36px;
		line-height: 1.3;
		@include medium;
		&:not(:last-child) {
			margin-bottom: 10px;
			@include md {
				margin-bottom: 16px; } }
		@include r(1520) {
			font-size: 32px;
			line-height: 1.2; }
		@include xs {
			font-size: 20px; } }
	&__link {
		text-decoration: none;
		color: inherit;
		transition: color 0.3s;
		position: relative;
		@include hover {
			color: #BEBDBD;
			.icon-arrow-right {
				opacity: 1;
				transform: translate(20px, -40%); } }
		.icon-arrow-right {
			fill: #BEBDBD;
			display: block;
			font-size: 20px;
			position: absolute;
			left: 100%;
			top: 50%;
			transform: translate(10px, -40%);
			opacity: 0;
			transition: opacity 0.3s, transform 0.3s; } } }
