.socials {
	display: flex;
	font-size: 23px;
	&__item {
		&:not(:last-child) {
			margin-right: 60px; } }
	&__link {
		display: block;
		color: inherit;
		position: relative;
		@include hover {
			&:after {
				transform: scaleX(1); } }
		&:after {
			content: '';
			height: 3px;
			width: 100%;
			background-color: $theme;
			position: absolute;
			bottom: -20px;
			transform: scaleX(0);
			transform-origin: 0 0;
			transition: transform 0.3s; }
		.icon {
			fill: #ffffff;
			display: block; } } }
