.menu-sidebar-item {
	& + & {
		margin-top: 100px;
		@include r(1520) {
			margin-top: 60px; }
		@include laptopSm {
			margin-top: 50px; }
		@include md {
			margin-top: 20px; }
		@include xs {
			margin: 0 15px; } }
	&__title {
		font-size: 20px;
		@include medium;
		line-height: 2;
		margin-bottom: 6px;
		@include laptopSm {
			margin-bottom: 3px;
			font-size: 14px; } }
	&__link {
		font-size: 30px;
		@include semi-bold;
		color: inherit;
		text-decoration: none;
		position: relative;
		@include laptopSm {
			font-size: 22px; }
		@include hover {
			text-decoration: none;
			.icon-arrow-right {
				transform: translateX(15px); } }
		.icon-arrow-right {
			transition: transform 0.3s;
			margin-left: 29px;
			fill: $theme;
			font-size: 20px;
			@include laptopSm {
				margin-left: 15px;
				font-size: 18px; }
			@include xs {
				margin-left: 0;
				margin-top: 15px;
				display: block; } }
		&_underline {
			text-decoration: underline; } }
	&_mobile-hidden {
		@include xs {
			display: none; } } }
