body {
	font-size: vw(20);
	line-height: 1.6;
	@include regular;
	color: #000;
	background-color: #ffffff;
	@include smooth-font;
	@include hd {
		font-size: 20px; }
	@include r(1500) {
		font-size: 16px; }
	@include md {
		font-size: 20px; }
	@include sm {
		font-size: 18px; }
	&.is-overflow {
		overflow: hidden; }
	&.customize-support {
		.header {
			top: 22px !important; } } }

.page-content {
	padding-top: 100px; }

.out {
	overflow-x: hidden; }


.loader {
	position: relative;
	width: 39px;
	height: 55px;
	margin: 10px auto; }

.loader-icon {
	position: absolute;
	top: 0;
	left: 0;

	&_sm {
		animation: star-small-pulse 1s ease-in-out alternate infinite;
		fill: #F9AA23; }

	&_big {
		animation: star-big-pulse 2s -0.2s ease-in-out infinite;
		fill: lighten(#F9AA23, 10); } }

@keyframes star-small-pulse {
	0% {
		transform: scale(1); }
	100% {
		transform: scale(2); } }

@keyframes star-big-pulse {
	0% {
		transform: scale(0);
		opacity: 1; }
	100% {
		transform: scale(5);
		opacity: 0; } }
