.team {
	$this: &;
	&__preloader {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: 1;
		display: none;
		@include md {
			top: 25%;
			transform: translate(-50%, 0); } }

	&__sliders {
		position: relative;
		&.with-preloader {
			#{$this}__preloader {
				display: block; } } }
	&__secondary-slider {
		position: relative; }
	&__slider-buttons {
		@include coverdiv;
		.container {
			height: 100%; } }
	&__slider-buttons-wrapper {
		height: 100%;
		position: relative;
		.btn-slick {
			z-index: 1;
			background: transparent;
			display: block;
			border: none;
			cursor: pointer;
			position: absolute;
			top: 40%;
			left: 0;
			font-size: 69px;
			opacity: 0.3;
			transition: opacity 0.3s;
			transform: translate(-100%, -50%);
			@include lg {
				transform: translate(-50%, -50%); }
			@include md {
				font-size: 52px;
				top: auto;
				bottom: 60px;
				left: 50%;
				transform: translate(-100px, 0);
				opacity: 0.1; }
			@include hover {
				opacity: 0.7; }
			.icon {
				display: block;
				fill: #ffffff;
				@include md {
					fill: #000; } }
			&_next {
				left: auto;
				right: 0;
				transform: translate(100%, -50%);
				@include lg {
					transform: translate(50%, -50%); }
				@include md {
					right: 50%;
					transform: translate(100px, 0); } } } } }

.team-main-slider {
	visibility: hidden;
	.slick-track {
		display: flex;
		@include md {
			display: block; } }
	.slick-slide {
		display: block;
		height: auto; }
	&.slick-initialized {
		visibility: visible; }
	&__item {
		position: relative;
		background-size: cover;
		background-position: center;
		color: #ffffff;
		@include md {
			background: #F7F5F2!important;
			color: #000;
			text-align: center;
			overflow: hidden; } }
	&__item-inner {
		padding: #{vw(210)}  #{vw(105)}  #{vw(300)};
		@include hd {
			padding: 201px 3% 300px; }
		@include md {
			padding: 54px 0 160px; } }
	&__title, &__text {
		position: relative;
		top: vw(-100);
		@include hd {
			top: -100px; }
		@include md {
			top: 0; } }
	&__photo {
		display: none;
		padding-top: 60%;
		position: relative;
		@include md {
			display: block; }
		@include xs {
			padding-top: 97%; }
		img {
			@include coverdiv;
			object-fit: cover;
			@include xs {
				width: auto;
				height: 100%;
				left: auto;
				right: -5%;
				object-position: right center; } } }
	&__photo-desk {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		@include md {
			display: none; }
		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__title {
		margin-bottom: 27px; }
	&__text {
		max-width: vw(680);
		@include medium;
		@include hd {
			max-width: 680px; }
		@include md {
			max-width: none; }
		p + p {
			margin-top: 25px; } } }

.team-secondary-slider {
	$this: &;
	padding: 0 5% 0 8.6%;
	position: absolute;
	bottom: 45px;
	left: 0;
	width: 100%;
	margin: 0 -25px;
	visibility: hidden;
	&.slick-initialized {
		visibility: visible; }
	@include lg {
		padding: 0 5% 0 3%; }
	@include laptopSm {
		padding: 0 5% 0 0; }
	@include md {
		display: none; }
	.slick-list {
		margin-right: -280px;
		padding-right: 280px; }
	&__slide {
		padding: 25px 30px;
		@include laptopSm {
			padding: 25px; }
		&.slick-current {
			#{$this}__item {
				transform: scale(1.05, 1.05);
				padding-top: calc(100% - 10px);
				border: 5px solid #ffffff;
				box-shadow: none;
 } }				// box-shadow: inset 0 0 13px 0 rgba(0,0,0,0.50)
		@include hover {
			&:not(.slick-current) {
				#{$this}__item {
					transform: translateY(-10px); } } } }
	&__item {
		transform-origin: 0 100%;
		transition: box-shadow 0.3s, transform 0.3s;
		cursor: pointer;
		background-size: cover;
		background-position: center;
		padding-top: 100%;
		width: 100%;
		border-radius: 50%;
		background-color: #D8D8D8;
		box-shadow: 0 8px 20px 1px rgba(0,0,0,0.50);
		position: relative;
		overflow: hidden;
		img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover; } } }
