.hero {
	background-size: cover;
	background-color: #B3241D;
	background-position: center;
	position: relative;
	z-index: 1;

	&__bg {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		@include sm {
			display: none; }
		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
			font-family: 'object-fit: cover'; } }
	&__inner {
		min-height: 100vh;
		min-height: calc(var(--vh) * 100);
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: relative;
		padding-top: 160px;
		padding-bottom: 160px;
		padding-right: vw(145);
		padding-left: 0;
		@include hd {
			padding-left: 145px;
			padding-right: 145px; }
		@include md {
			padding: 130px 0 160px; }
		@include sm {
			padding: 130px 0 80px;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: center; }
		@include r(400) {
			padding-top: 80px;
			padding-bottom: 40px; } }
	&__title {
		max-width: 600px;
		margin-bottom: vw(85);
		color: #ffffff;
		@include hd {
			margin-bottom: 85px; }
		@include r(1320) {
			max-width: 450px; }
		@include md {
			max-width: 450px;
			margin-bottom: 50px; }
		@include sm {
			text-align: center;
			margin: 0 auto 40px;
			max-width: 360px; }
		@include r(400) {
			height: 25vh;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center; } }
	&__scroll-btn {
		z-index: 1;
		position: absolute;
		bottom: 44px;
		left: 50%;
		transform: translateX(-50%);
		font-size: vw(93);
		animation: scroll 1.5s infinite ease-in-out;
		@include hd {
			font-size: 93px; }
		@include md {
			font-size: 76px; }
		@include sm {
			display: none; }
		.icon-mouse {
			display: block;
			fill: #ffffff; } }
	&__links {
		position: absolute;
		left: 0;
		bottom: 45px;
		width: 100%;
		display: flex;
		justify-content: space-between;
		@include sm {
			display: none; } }
	&__link {
		color: #ffffff;
		text-decoration: none;
		@include semi-bold;
		font-size: 24px;
		line-height: 1.67;
		display: block;
		padding-left: 55px;
		position: relative;
		@include md {
			font-size: 20px;
			padding-left: 35px; }
		@include hover {
			.icon {
				transform: translate(-20px, -50%); } }
		.icon {
			position: absolute;
			top: 50%;
			left: 0;
			transform: translate(0, -50%);
			font-size: 20px;
			fill: #ffffff;
			transition: transform 0.3s;
			@include md {
				font-size: 15px; } }
		&_right {
			padding-right: 55px;
			padding-left: 0;
			@include md {
				padding-right: 35px; }
			@include hover {
				.icon {
					transform: translate(20px, -50%); } }
			.icon {
				left: auto;
				right: 0; } } }
	&__button {
		@include sm {
			text-align: center; } }
	&__bg-mobile {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		display: none;
		z-index: -1;
		@include sm {
			display: block; }
		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover; } } }

@keyframes scroll {
	from {
		transform: translate(-50%, 10px); }
	50% {
		transform: translate(-50%, 0); }
	to {
		transform: translate(-50%, 10px); } }



