@include font(Montserrat-Regular, Montserrat-Regular);
@include font(Montserrat-SemiBold, Montserrat-SemiBold);
@include font(Montserrat-Medium, Montserrat-Medium);

@mixin regular {
	font-family: Montserrat-Regular, Arial, sans-serif;
	font-style: normal;
	font-weight: normal; }

@mixin semi-bold {
	font-family: Montserrat-SemiBold, Arial, sans-serif;
	font-style: normal;
	font-weight: normal; }

@mixin medium {
	font-family: Montserrat-Medium, Arial, sans-serif;
	font-style: normal;
	font-weight: normal; }

