.title {
	&_h1 {
		font-size: vw(110);
		line-height: 1;
		@include regular;
		@include hd {
			font-size: 110px; }
		@include md {
			font-size: 60px; }
		@include sm {
			font-size: 52px; } }
	&_h2 {
		line-height: 1.33;
		@include medium;
		font-size: vw(60);
		@include hd {
			font-size: 60px; }
		@include md {
			font-size: 40px;
			line-height: 1.4; }
		@include r(413) {
			font-size: 36px; } }
	&_align-center {
		text-align: center; } }

