.contacts-section {
	padding: #{vw(248)} 0;
	background-color: #F7F5F2;
	background-image: url("../img/contacts.svg");
	background-size: 100% 100%;

	@include hd {
		padding: 248px 0; }
	@include md {
		padding: 180px 0 80px; }
	@include xs {
		padding: 100px 0 80px;
		background-size: cover;
		background-position: center; }
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		@include md {
			flex-wrap: wrap;
			display: block;
			text-align: center;
			max-width: 400px;
			margin: 0 auto; } }
	&__title {
		max-width: 37%;
		position: relative;
		@include lg {
			max-width: 42%; }
		@include laptopSm {
			padding-right: 0;
			margin-bottom: 40px; }
		@include md {
			max-width: 100%; }
		span {
			position: relative; }
		.icon {
			position: absolute;
			right: vw(-70);
			bottom: vw(15);
			font-size: vw(30);
			fill: #000;
			@include hd {
				right: -70px;
				font-size: 30px;
				bottom: 15px; }
			@include md {
				display: none; } } }

	&__content {
		max-width: 340px;
		margin: 0 20px;
		@include laptopSm {
			margin-left: 0; }
		@include md {
			max-width: none;
			margin: 0 0 30px; }
		a {
			color: $theme;
			@include hover {
				text-decoration: none; } } }
	&__title-sm {
		@include semi-bold;
		font-size: 28px;
		line-height: 1.28;
		margin-bottom: 30px; }
	&__socials {
		max-width: 360px;
		margin: 0 20px;
		@include md {
			max-width: none;
			margin: 0;
			display: flex;
			flex-direction: column;
			align-items: center; } }
	&__theme-title {
		@include semi-bold;
		line-height: 1.5;
		color: $theme;
		margin-bottom: 25px;
		@include md {
			margin-bottom: 30px; } } }
