.testimonial {
	max-width: 1090px;
	margin: 0 auto;
	text-align: center;
	@include laptopSm {
		max-width: 720px; }
	&__photo {
		height: 140px;
		width: 140px;
		border-radius: 50%;
		overflow: hidden;
		margin: 0 auto  30px;
		img {
			height: 100%;
			width: 100%;
			object-fit: cover; } }
	&__name {
		@include semi-bold;
		font-size: 28px;
		line-height: 1.2;
		@include xs {
			font-size: 24px; } }
	&__position {
		@include semi-bold;
		font-size: 20px;
		line-height: 1.7;
		color: $theme;
		margin-bottom: vw(62);
		@include hd {
			margin-bottom: 62px; }
		@include xs {
			font-size: 18px;
			margin-bottom: 37px; } }
	&__text {
		font-size: vw(30);
		line-height: 1.4;
		margin-bottom: vw(47);
		@include hd {
			margin-bottom: 47px;
			font-size: 30px; }
		@include md {
			margin-bottom: 47px;
			font-size: 30px; }
		@include xs {
			font-size: 24px;
			line-height: 1.5;
			margin-bottom: 10px; } } }

