.burger {
	cursor: pointer;
	width: 42px;
	@include laptopSm {
		width: 32px; }
	span {
		display: block;
		height: 4px;
		background-color: #ffffff;
		border-radius: 3px;
		margin: auto 0 auto auto;
		transition: transform 0.3s, width 0.3s, opacity 0.3s, background-color 0.3s;
		@include laptopSm {
			height: 3px; }
		&:not(:last-child) {
			margin-bottom: 8px;
			@include laptopSm {
				margin-bottom: 4px; } }
		&:nth-child(1) {
			width: 32px;
			transform-origin: 100% 0;
			@include laptopSm {
				width: 24px; } }
		&:nth-child(2) {
			width: 100%; }
		&:nth-child(3) {
			width: 24px;
			transform-origin: 100% 100%;
			@include laptopSm {
				width: 18px; } } }
	@include hover {
		span {
			width: 100%; } }
	&.is-active {
		span {
			width: 100%;
			&:nth-child(1) {
				transform: rotate(-45deg) translate(-4px, -7px);
				@include laptopSm {
					transform: rotate(-45deg) translate(-3px, -7px); } }
			&:nth-child(2) {
				transform: translateX(100%);
				opacity: 0; }
			&:nth-child(3) {
				transform: rotate(45deg) translate(-5px, 7px);
				@include laptopSm {
					transform: rotate(45deg) translate(-2px, 8px); } } } } }

