.testimonials {
	padding: #{vw(200)} 0 #{vw(180)};
	@include hd {
		padding: 200px 0 180px; }
	@include sm {
		padding: 120px 0 180px; }
	@include xs {
		padding: 60px 0 120px; }
	&__title {
		margin-bottom: vw(62);
		@include hd {
			margin-bottom: 62px; }
		@include xs {
			margin-bottom: 30px; } } }

.testimonial-main-slider {
	.btn-slick {
		z-index: 1;
		background: transparent;
		display: block;
		border: none;
		cursor: pointer;
		position: absolute;
		top: 50%;
		left: 0;
		font-size: 69px;
		opacity: 0.1;
		transition: opacity 0.3s;
		transform: translate(-100%, -50%);
		@include lg {
			transform: translate(-50%, -50%); }
		@include md {
			font-size: 52px; }
		@include sm {
			font-size: 52px;
			top: auto;
			bottom: -210px;
			left: 50%;
			transform: translate(-100px, 0); }
		@include xs {
			bottom: -170px; }
		@include hover {
			opacity: 0.3; }
		.icon {
			display: block;
			fill: #000; }
		&_next {
			left: auto;
			right: 0;
			transform: translate(100%, -50%);
			@include lg {
				transform: translate(50%, -50%); }
			@include sm {
				right: 50%;
				transform: translate(100px, 0); } } } }

.testimonial-secondary-slider {
	$this: &;
	.slick-track {
		display: flex;
		align-items: center; }
	&__slide {
		padding: 20px 30px;
		text-align: center;
		font-size: 0;
		opacity: 0.6;
		filter: grayscale(1);
		transition: opacity  0.3s, filter 0.3s;
		cursor: pointer;
		img {
			display: inline-block;
			max-width: 100%; }
		&.slick-current {
			opacity: 1;
			filter: none; } } }

