.advantage {
	$this: &;
	padding: #{vw(90)} 0;
	@include hd {
		padding: 90px 0; }
	@include md {
		padding: 90px 0 50px; }
	&__inner {
		display: flex;
		align-items: center;
		padding: 0;
		@include hd {
			padding: 0 0 0 8.6%; }
		@include md {
			flex-direction: column-reverse; } }
	&__content {
		width: 55%;
		position: relative;
		padding-right: 17%;
		min-width: vw(770);
		@include hd {
			min-width: 770px; }
		@include md {
			min-width: auto;
			width: auto;
			padding-right: 0; }
		@include xs {
			text-align: center; } }
	&__title {
		margin-bottom: vw(46);
		@include hd {
			margin-bottom: 46px; }
		@include md {
			margin-bottom: 46px; }
		.title_h2 {
			@include r(413) {
				font-size: 28px; } } }
	&__text {
		margin-bottom: vw(97);
		@include hd {
			margin-bottom: 97px; }
		@include md {
			margin-bottom: 40px; } }
	&__buttons {
		display: flex;
		flex-wrap: wrap;
		margin: -15px -30px;
		@include xs {
			flex-direction: column;
			align-items: center; }
		.btn-sm {
			margin: 15px 30px; } }
	&__image-container {
		width: 39%;
		position: relative;
		@include md {
			width: auto;
			max-width: 400px;
			margin-bottom: 40px; }
		@include xs {
			max-width: 250px; }

		&:before {
			z-index: -1;
			content: '';
			position: absolute;
			bottom: -40px;
			left: 0;
			height: 40px;
			width: 100%;
			border-radius: 50%;
			transform: scale(0.7);
			transform-origin: 50% 50%;
			background-image: radial-gradient(50% 50%, #D4D6DC 5%, rgba(255,255,255,0) 100%);
			animation: girl-shadow 3s infinite; }

		&_3 {
			@include md {
				width: 400px; }
			&::before {
				left: 10%; } } }
	&__image-group {
		position: relative;

		&_1 {
			max-width: 550px;
			#{$this}__image-bg {
				top: 6%;
				@include xs {
					top: 20%;
					max-width: 120%;
					left: -5%; } }
			#{$this}__image-elems {
				max-width: 120%;
				top: 6%;
				@include xs {
					left: -8%;
					max-width: 120%;
					top: 19%; } } }
		&_2 {
			max-width: 475px;
			margin-left: auto;
			margin-right: auto;
			#{$this}__image-bg {
				top: -23%;
				left: -12%;
				max-width: 123%; }
			#{$this}__image-elems {
				max-width: 120%;
				top: -27%;
				left: -9%; } }
		&_3 {
			max-width: 100%;
			#{$this}__image {
				max-width: 370px;
				margin-left: auto;
				margin-right: auto;
				@include r(1450) {
					max-width: 300px; }
				@include md {
					max-width: 280px; }
				@include xs {
					max-width: 180px; } }
			#{$this}__image-bg {
				top: 10%;
				left: 0;
				max-width: 100%; }
			#{$this}__image-elems {
				max-width: 100%;
				top: -7%;
				left: 0; } } }

	&__image-bg, &__image-elems {
		top: 0;
		left: 0;
		position: absolute;
		max-width: 100%; }

	&__image-elems {
		animation: elems 4.5s infinite linear; }

	&__image {
		width: 100%;
		display: block;
		animation: girl 4.5s infinite linear;
		position: relative;
		z-index: 1; }
	&__bracket {
		position: absolute;
		top: 0;
		right: 8%;
		display: block;
		font-size: vw(280);
		line-height: 0.68;
		@include hd {
			font-size: 280px; }
		@include md {
			display: none; } }
	&_reverse {
		#{$this}__inner {
			padding: 0;
			flex-direction: row-reverse;
			justify-content: flex-end;
			@include md {
				flex-direction: column-reverse;
				justify-content: flex-start; } }
		#{$this}__content {
			padding-right: 0;
			padding-left: 17%;
			@include laptopSm {
				padding-right: 0;
				padding-left: 10%; }
			@include md {
				padding: 0; } }
		#{$this}__bracket {
			transform-origin: 50% 50%;
			transform: rotate(180deg);
			right: auto;
			left: 8%;
			@include laptopSm {
				left: 0; } } }
	&__soon {
		z-index: 5;
		white-space: nowrap;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		@include semi-bold;
		font-size: vw(30);
		line-height: 1.33;
		background-color: rgba(255, 255, 255, 0.9);
		border-radius: 4px;
		padding: #{vw(28)} #{vw(80)};
		@include hd {
			font-size: 30px;
			padding: 28px 80px; }
		@include md {
			padding: 15px 40px;
			font-size: 24px; } } }

@keyframes girl {
	0% {
		transform: translateY(0); }
	50% {
		transform: translateY(-2.5%); }
	100% {
		transform: translateY(0); } }

@keyframes elems {
	0% {
		transform: translateY(0); }
	50% {
		transform: translateY(2.5%); }
	100% {
		transform: translateY(0); } }

@keyframes girl-shadow {
	0% {
		transform: scale(0.7); }
	50% {
		transform: scale(0.4); }
	100% {
		transform: scale(0.7); } }
